import React from "react"
import photo from "../images/photo_walid.jpg"

const Apropo = () => (
<section className="sec-grid">
    <div className="sec-img">
    <section className="animation-box">
        <img className="text-effect-right" src={photo} alt="Contient le Portrait de Walid Khaladi"></img>
        <figcaption className="subtitle title text-effect">&rdquo; ...pris entrain de rire du serveur qui est tombé dans l'escalier.&rdquo;</figcaption>
    </section>
    </div>
    <div className="sec-text">
        <section className="animation-box">
            <h2 className="title text-effect-right">Walid Khaladi</h2>
            <p className="text-effect">Né et grandit en Algérie. Walid Khaladi a commencé à photographier pour la première fois à l'âge de 14 ans - influencé par l'autoportrait et la photographie conceptuelle.</p>
            <p className="text-effect-right"> Grandir avec la montée des médias sociaux et des techniques numériques a permis à Walid d’avoir une vision plus large d’une industrie internationale. Après avoir obtenu son diplôme de photographe, il a commencé à travailler avec les écoles, où il a naturellement senti le passage à la photographie de mode. Quelques années plus tard.</p>
            <p className="text-effect">Walid aime créer des images intemporelles et émotives. il est inspiré par la force des médias digitale, la beauté unique et le style classique.</p>
            <p className="text-effect-right">Il vit actuellement à Alger-Centre, et voyage régulièrement pour des missions ou des sorties en plein nature, le fait d’être également à l’aise devant l’objectif a donné lieu à de nombreuses apparitions à la télévision et au numérique.</p>
            <p className="text-effect"> - <strong>Clients</strong> - </p>
            <p className="text-effect-right"> - <strong>Publications</strong> - </p>
        </section>
    </div>
</section>
)

export default Apropo